.market-item-container .notice-name,
#awd-component-root body,
#awd-component-root input,
#awd-component-root div,
#awd-component-root textarea {
    font-size: 16px;
    font-family: 'Arial', sans-serif;
}

.right-third {
    justify-content: flex-end;
}

.right-third button:last-child {
    margin-left: auto;
}

.message-display-container {
    text-align: center;
    overflow: auto;
    border-bottom: 1px dashed black;
    font-weight: bold;
    padding: 10px;
}

.multiline-textarea {
    display: flex;
    align-items: center;
    gap: 10px;
}

.multiline-textarea label {
    flex-shrink: 0;
}

.multiline-textarea textarea {
    flex-grow: 1;
    width: 100%;
    resize: vertical;
}

.display_none {
    display: none;
}

#weeklyInterval {
    width: 45px;
    margin-left: 0px;
    margin-right: 0px;
}

.market-item-container input[type="text"] {
    width: 100%;
}

@media (max-width: 600px) {
    .market-item-container {
        padding: 5px;
    }

    .market-item-container label {
        width: 100%;
        text-align: left;
        margin-right: 0;
    }

    .market-item-container input[type="text"] {
        width: 100%;
        max-width: none;
    }

    .button-container {
        flex-direction: row;
    }

    .button-container button {
        width: 100%;
        margin: 5px 0;
    }

    .filter-input {
        width: 100%;
    }

    .filter-results {
        width: 100%;
    }

    .sticky-header {
        height: auto;
    }

}

.market-item-container {
    border-bottom: 1px dashed black;
    width: 100%;

    border: 1px solid #ccc;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    transition: all 0.3s ease;
    box-sizing: border-box;
    padding: 10px;
}

.market-item-container:focus-visible {
    outline: none;
    border: 1px solid blue;
    box-shadow: 0 0 0 1px blue;
}

#awd-component-root button {
    margin-top: 10px;
    background-color: #007BFF;
    color: white;
    border: none;
    padding: 10px;
    border-radius: 5px;
}

#awd-component-root .list-container button {
    cursor: pointer;
    transition: background-color 0.3s ease, color 0.3s ease;
}

#awd-component-root button:enabled {
    background-color: #007BFF;
    color: white;
    transition: background-color 0.3s ease, color 0.3s ease;
}

#awd-component-root button:enabled:hover {
    background-color: #0056b3;
}

#awd-component-root button:disabled {
    background-color: #e0e0e0;
    color: #a0a0a0;
}

.market-item-container label {
    display: inline-block;
    width: 250px;
    text-align: right;
    margin-right: 10px;
}

.market-item-container input[type="text"] {
    width: 95%;
}

.market-item-container input[type="text"],
.market-item-container input[type="checkbox"],
.market-item-container input[type="number"],
.market-item-container input[type="date"] {
    margin-left: 10px;
}

.market-item-container.editing {
    background-color: #FFFFE0;
}

.market-item-container.active {
    background-color: #ebfbff;
}

.market-item-container.inactive {
    background-color: #f7f7f7;
}

@keyframes scale-up-ver-top {
    0% {
        transform: scaleY(1);
        transform-origin: 100% 0%;
    }

    100% {
        transform: scaleY(1);
        transform-origin: 100% 0%;
    }
}

@keyframes scale-down-ver-top {
    0% {
        transform: scaleY(1);
        transform-origin: 100% 0%;

        .market-item-container.collapsed {
            max-height: 40px;
            overflow: hidden;
            cursor: pointer;
            animation: scale-down-ver-top 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) 0s 1 normal both;
        }
    }
}

.scale-up-ver-top-normal {
    animation: scale-up-ver-top 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) 0s 1 normal both;
}

.market-item-container.collapsed {
    overflow: hidden;
    cursor: pointer;
}

.market-item-container.expanded {
    height: auto;
    animation: scale-up-ver-top 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) 0s 1 normal both;
}

.market-item-container.editing.expanded {
    animation: none;
}

.market-item-container .notice-name {
    font-weight: bold;
    padding: 10px;
}

.market-item-container:hover {
    max-height: 1000px;
    overflow: visible;
    box-shadow: inset 0 0 5px rgba(0, 0, 255, 0.5);
    transition-delay: .1s;
}

.market-item-container .multiple-date-manager,
.schedule {
    border: 1px dashed grey;
    padding: 5px;
    border-radius: 5px;
    margin-top: 2px;
}

.market-item-container .multiple-date-manager input {
    margin-left: 270px;
}

.market-item-container .multiple-date-manager .add-button,
.market-item-container .multiple-date-manager .remove-button {
    width: 20px;
    font-weight: bold;
}

.market-item-container .multiple-date-manager .add-button {
    margin-left: 402px;
}

.market-item-container .multiple-date-manager .remove-button {
    margin-left: 5px;
}

.default-message-container {
    margin: 10px 0;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    transition: background-color 0.3s ease;
    cursor: pointer;
}

.default-message-container:hover {
    background-color: #f7f7f7;
}

.default-message-container.editing {
    background-color: lightyellow;
}

.default-message-container.collapsed {
    background-color: #fff;
}

.default-message-alias {
    font-weight: bold;
    margin-bottom: 5px;
}

.button-container {
    width: auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

.button-container button {
    width: 150px;
    height: 40px;
    margin: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5px;
}

.icon {
    width: 16px;
    height: 16px;
}

.sticky-header {
    line-height: 25px;
    text-align: center;
    width: 100%;
    position: fixed;
    top: 0;
    background-color: lightgreen;
    z-index: 1;
    height: 180px;
}

.content-container {
    overflow-y: auto;
    /* Enable vertical scrolling */
    height: calc(100vh - 180px);
    /* Adjust height to account for the sticky header */
    top: 190px;
    /* Position below the sticky header */
    position: relative;
    width: 100%;
}

.save-failed {
    background-color: lightyellow;
    margin-top: 5px;
    padding-top: 5px;
}

.content-container-col2 {
    display: none;
    width: 100%;
    position: relative;
    top: auto;
    right: auto;
}

/* Debug Styles */
.debug-width {
    width: 100%;
    height: 1000px;
    visibility: hidden;
}

.debug {
    width: 100%;
    visibility: hidden;
}

.filter-container {
    position: relative;
}

.market-name,
.market-name input[type="text"] {
    font-weight: bold;
}

.filter-bar {
    width: 100%;
    display: inline-block;
}

.filter-input {
    width: 50%;
}

.filter-results {
    width: 33.33%;
    display: inline-block;
}

.ord-checkbox-container.horizontal {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}

.ord-checkbox-container.horizontal .ord-checkbox {
    display: flex;
    align-items: center;
    width: unset;
    margin-right: unset;
}

.label-div-container {
    display: flex;
    align-items: center;
}

.check-box-container {
    display: block ruby;
    text-align: center;
}

@media (min-width: 375px) {
    .container {
        width: 100%;
        padding: 20px;
    }

    .button-container button {
        width: 110px;
        height: 40px;
        margin: 5px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 5px;
    }

    .market-item-container label {
        display: inline-block;
        width: 55px;
        text-align: right;
        margin-right: 10px;
    }
}



@media (min-width: 768px) {

    .market-item-container .notice-name,
    #awd-component-root body,
    #awd-component-root input,
    #awd-component-root div,
    #awd-component-root textarea {
        font-size: 18px;
        font-family: 'Arial', sans-serif;
    }

    .container {
        width: 80%;
        padding: 30px;
    }
}

@media (min-width: 1024px) {

    .market-item-container .notice-name,
    #awd-component-root body,
    #awd-component-root input,
    #awd-component-root div,
    #awd-component-root textarea {
        font-size: 20px;
        font-family: 'Arial', sans-serif;
    }

    .container {
        width: 70%;
        padding: 40px;
    }
}